.PokeCard--container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 85%;
  max-width: 32rem;
  min-width: 24rem;
  min-height: 300px;
  padding: 1rem;
  color: white;
  background-color: #ff1f55;
  border-radius: 0.8rem;
  list-style: none;

  .Pokemon-number {
    font-size: 1.6rem;
    text-align: right;
  }

  .Pokemon-name {
    font-size: 2rem;
  }

  figure {
    background: white;
    border-radius: 0.8rem;
    a {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        object-fit: cover;
      }
    }
  }
}

.Poketypes {
  display: flex;
  // justify-content: center;
  align-items: center;
  gap: 1.6rem;
}

.type {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 3rem;
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
  border-radius: 0.4rem;
}

